import React, { Component } from 'react';
import { Badge, Card, CardBody, CardFooter, CardHeader, Col, Row, Collapse, Fade, Jumbotron, Button, ButtonToolbar, ButtonGroup, Carousel, CarouselItem, Container } from 'reactstrap';
import Widget04 from './../../Widgets/Widget04';

import logo from '../../../assets/img/summer.jpg'

export class Song extends Component {
	constructor(props) {
		super(props);

		this.state = {
			play: false
		}

		this.togglePlay = this.togglePlay.bind(this);
	}

	audio = new Audio(`http://api.ownradio.ru/v4/tracks/${this.props.track.id}`)

	togglePlay = () => {
		this.setState({ play: !this.state.play }, () => {
			this.state.play ? this.audio.play() : this.audio.pause();
		});
	}

	render() {

		let { artist, name, id } = this.props.track;

		return (
			<Col sm="6" md="2">
				<div className="ownradio-song">
					<div className="ownradio-song-image">
						<img src={logo} alt="admin@bootstrapmaster.com" />
					</div>
					<div className="ownradio-toolbar">
						<ButtonToolbar>
							<Button outline color="primary" size="sm" onClick={this.togglePlay}>{this.state.play ? <i className="fa fa-pause"></i> : <i className="fa fa-play"></i>}</Button>&nbsp;
							<Button outline color="primary" size="sm" ><i className="fa fa-plus"></i></Button>&nbsp;
							<Button outline color="primary" size="sm" ><i className="fa fa-heart"></i></Button>&nbsp;
							<Button outline color="primary" size="sm" ><i className="icon-options-vertical"></i></Button>
						</ButtonToolbar>
					</div>
					<div className="ownradio-song-artist">
						<a href="#"><i className="fa fa-music"></i> {artist} - <span className="ownradio-song-track">{name}</span></a>
					</div>
					{/*<div className="ownradio-song-track">
						<a href="#"><small>{name}</small></a>
					</div>*/}
				</div>

				{/*<Widget04 icon="icon-control-play" color="success" header="XXXTentacion" value="100">Bad!</Widget04>*/}
			</Col>
		);
	}
}
